function showUnloginModal() {
	uni.showModal({
		title: '',
		content: '~',
		confirmText: "",
		cancelText: "",
		success: res2 => {
			if (res2.confirm) {
				uni.navigateTo({
					url: "/pages/public/login"
				});
			}
		}
	});
}

module.exports = {
    showUnloginModal: showUnloginModal
}